var visit = require('unist-util-visit');

var katex = require('katex').renderToString;

var unified = require('unified');

var parse = require('rehype-parse');

var toText = require('hast-util-to-text');

module.exports = rehypeKatex;
var assign = Object.assign;
var parseHtml = unified().use(parse, {
  fragment: true,
  position: false
});
var source = 'rehype-katex';

function rehypeKatex(options) {
  var settings = options || {};
  var throwOnError = settings.throwOnError || false;
  return transformMath;

  function transformMath(tree, file) {
    visit(tree, 'element', onelement);

    function onelement(element) {
      var classes = element.properties.className || [];
      var inline = classes.includes('math-inline');
      var displayMode = classes.includes('math-display');

      if (!inline && !displayMode) {
        return;
      }

      var value = toText(element);
      var result;

      try {
        result = katex(value, assign({}, settings, {
          displayMode: displayMode,
          throwOnError: true
        }));
      } catch (error) {
        var fn = throwOnError ? 'fail' : 'message';
        var origin = [source, error.name.toLowerCase()].join(':');
        file[fn](error.message, element.position, origin);
        result = katex(value, assign({}, settings, {
          displayMode: displayMode,
          throwOnError: false,
          strict: 'ignore'
        }));
      }

      element.children = parseHtml.parse(result).children;
    }
  }
}